body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
    width: 100%;
    height: 100%;
}

body {
    background-repeat: repeat;
    background-size: contain;
    background-image: url(/audioscope/static/media/bg.997dca6e.jpg);
}

header {
    background: white;
    text-align: center;
    line-height: 50px;
    margin-bottom: 20px;
}

header img {
    max-height: 50px;
    display: inline;
    margin: 0 10px;
}

header span {
    vertical-align: top;
}

canvas {
    border: solid 1px black;
    padding-left: 0;
    padding-right: 0;
    margin-left: auto;
    margin-right: auto;
    display: block;
    background: black;
    background-image: url(/audioscope/static/media/chuck.762d81de.gif);
    background-size: contain;
    background-repeat: no-repeat;
    background-position-y: 50%;
}

.tv {
    background-image: url(/audioscope/static/media/tv.0ee65bfe.png);
    background-color: black;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    display: block;
    background-position-x: center;
    padding-top: 25px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.rom-loader {
    text-align: center;
    margin-top: 20px;
}

span.right {
    float: right;
}

.command-list {
    background: white;
}

.command-list span {
    font-weight: bolder;
}

.command-list ul {
    list-style: none;
    display: inline;
}

.command-list li {
    margin-right: 15px;
    display: inline;
    font-style: italic;
}
